* {
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
}

nav {
    height: 70px;
    display: flex;
    padding: 20px 20px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.nav-logo {
    max-width: 100px;
}

.hero-container {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.hero-container > h1 {
    /* font-size: 26px; */
    font-size: 36px;
    line-height: 1.1;
    font-weight: 600;
    margin-bottom: 10px;
}

.hero-container > p {
    max-width: 350px;
    margin: 5px 0;
}

.hero-container > img {
    max-width: 100%;
    max-height: 100%;
    align-self: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

/* Tablet Query  */
@media screen and (min-width: 500px) {
}

/* Desktop Query */
@media screen and (min-width: 1025px) {

  nav {
    padding: 20px 150px;
  }
  
  .hero-container > h1, .hero-container > p {
    margin-left: 130px;
  }

  .hero-container > h1 {
    font-size: 52px;
  }

  .hero-container h2 {
    font-size: 22px;
  }
}